import styled from "styled-components"

/**
 * The magic button with one element, challenge accepted!
 */
export const TheButton = styled.button`
  border: 2px solid #b5b5b5;
  background: #f2a54e;
  width: 11em;
  height: 6em;
  padding: 1em;
  font-size: 1.5em;
  font-weight: 900;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;

  &:after {
    width: 2.5em;
    height: 2.5em;
    content: "X";
    background: white;
    border-radius: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.5em;
    font-weight: bold;
  }
`

export const P = styled.p`
  font-size: 1.25em;
  color: #2e2e2e;
  margin-bottom: 2em;
`