/* eslint-disable react/jsx-pascal-case */
import React from 'react'
import { Route, Routes } from 'react-router-dom'
import Sandbox from './components/Sandbox'
import Home from './pages/homepage/Home'
import Task_01 from './pages/task_01/Task_01'
import Task_02 from './pages/task_02/Task_02'
import Task_03 from './pages/task_03/Task_03'

const Router = () => {
  return (
    <>
      <Routes>
        <Route path='/'>
          <Route index element={<Home />} />
          <Route path='task-01' element={<Task_01 />} />
          <Route path='task-02' element={<Task_02 />} />
          <Route path='task-03' element={<Task_03 />} />
          <Route path='sandbox' element={<Sandbox />} />
          <Route path='*' element={<h1>404</h1>} />
        </Route>
      </Routes>
    </>
  )
}

export default Router
