import styled from 'styled-components'

export const Section = styled.section`
  display: flex;
  justify-content: space-between;
  align-items: stretch;

  @media (max-width: 800px) {
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
  }
`

export const SearchWrapper = styled.section`
  padding: 2em 1em;
  width: 30em;
  background-color: #f9f9f9;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
  

  @media (max-width: 800px) {
    width: 100%;
  }
`

export const UserCard = styled.article`
  padding: 2em 1em;
  width: 30em;
  background-color: #f9f9f9;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  
  @media (max-width: 800px) {
    width: 100%;
    margin-top: 2em;
  }
`

export const UserCardRow = styled.div`
  padding: 1em 0;
  border-bottom: 1px solid lightgray;
  width: 100%;
`

export const UserKey = styled.strong`
  color: black;
`

export const UserValue = styled.p`
  color: gray;
  font-size: 0.85em;
  display: inline;
`

export const Input = styled.input`
  padding: 1em;
  border-radius: 5px;
  border: none;
  background-color: white;
  box-shadow: rgba(0, 0, 0, 0.02) 0px 1px 3px 0px,
    rgba(27, 31, 35, 0.15) 0px 0px 0px 1px;
  width: 100%;

  &:focus-visible {
    outline: 1px solid #198754;
    color: #198754;
  }
`

export const ResultsWrapper = styled.section`
  margin: 1em 0;
  background-color: white;
  border-radius: 5px;
  width: 100;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  display: ${({ visible }) => (visible ? 'flex' : 'none')};
  max-height: 33em;
  overflow: auto;
`

export const UsersList = styled.ul`
  display: flex;
  flex-direction: column;
  padding: 0;
  list-style-type: none;
  width: 100%;
`

export const Loading = styled.p`
  padding: 1em;
  color: gray;
`

export const UserRow = styled.li`
  padding: 1em;
  width: 100%;
  transition: all ease-in 0.1s;

  &:hover, &:focus-visible {
    background-color: #aee6c057;
    outline: none;
  }
`

export const UserButton = styled.button`
  color: #444444;
  font-size: 1.05em;
  text-align: left;
  cursor: pointer;
  background: transparent;
  border: none;
  width: 100%;
`

export const SearchHistoryRow = styled.section`
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 1.5em 0.5em;
  border-bottom: 1px solid lightgray;

  strong {
    font-size: 1.15em;
    font-weight: 700;
  }

  button {
    text-decoration: underline;
    font-size: 0.85em;
    color: #505050;
    background: transparent;
    border: none;
    cursor: pointer;
  }
`

export const SearchHistoryList = styled.ul`
  padding: 0;
  list-style-type: none;
`

export const SearchHistoryItem = styled.li`
  display: flex;
  width: 100%;
  align-items: center;
  border-bottom: 1px solid lightgray;
  padding: 0.5em 0;
`

export const SearchHistory = styled.button`
  background: transparent;
  border: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  width: 100%;

  strong {
    color: black;
    font-size: 0.95em;
  }

  span {
    font-size: 0.5;
    color: gray;
    font-weight: lighter;
  }
`

export const RemoveSearch = styled.button`
  font-size: 1em;
  color: gray;
  background: transparent;
  border: none;
  cursor: pointer;

  span {
    font-size: 0.85em;
  }
`
