import PropTypes from 'prop-types'
import { UserCard, UserCardRow, UserKey, UserValue } from '../searchBar_styles'

/**************************************
 ******** Render user card
 *************************************/
const RenderUserCard = ({ selectedUser }) => {
  if (Object.keys(selectedUser).length) {
    const {
      name,
      username,
      email,
      address: { suite, street, city },
    } = selectedUser

    return (
      <UserCard>
        <UserCardRow>
          <UserKey>Name: </UserKey>
          <UserValue>{name}</UserValue>
        </UserCardRow>

        <UserCardRow>
          <UserKey>Username: </UserKey>
          <UserValue>{username}</UserValue>
        </UserCardRow>

        <UserCardRow>
          <UserKey>Email: </UserKey>
          <UserValue>{email}</UserValue>
        </UserCardRow>

        <UserCardRow>
          <UserKey>Address: </UserKey>
          <UserValue>
            {suite}, {street}, {city}
          </UserValue>
        </UserCardRow>
      </UserCard>
    )
  } else {
    return <></>
  }
}

RenderUserCard.propTypes = {
  selectedUser: PropTypes.object.isRequired,
}

export default RenderUserCard
