import React from 'react'
import { Link } from 'react-router-dom'
import { Button, ButtonsRow, Main, MainTitle } from './home_styles'

const Home = () => {
  return (
    <>
      <Main>
        <MainTitle>Soltia Test Task</MainTitle>

        <ButtonsRow>
          <Link to='/task-01'>
            <Button>
              <span>Task 01</span>
            </Button>
          </Link>

          <Link to='/task-02'>
            <Button>
              <span>Task 02</span>
            </Button>
          </Link>

          <Link to='/task-03'>
            <Button>
              <span>Task 03</span>
            </Button>
          </Link>
        </ButtonsRow>
      </Main>
    </>
  )
}

export default Home
