import React from 'react'
import { useEffect } from 'react'
import { useRef } from 'react'
import Accordion from '../../components/accordion/Accordion'
import { PageTitle } from '../../styles/globalStyles'
import { Answer, Item, List, QApair, Question } from './task_02_styles'

const Task_02 = () => {
  // store reference to page title element
  const titleRef = useRef()

  /**
   * focus page title on component mount because otherwise
   * since we are creating a single page application
   * the screen reader won't notice the page content is changed
   */
  useEffect(() => {
    titleRef.current.focus()
  }, [])

  return (
    <>
      <main id='main-content'>
        <PageTitle ref={titleRef} tabIndex={-1}>
          Task 02
        </PageTitle>

        <List>
          <Item>
            <Accordion
              triggerId='question-01-button'
              bodyId='question-01-body'
              title='Question 01'
            >
              <QApair>
                <Question>
                  How would you describe the difference between HTML, CSS and
                  JavaScript?
                </Question>

                <Answer>
                  <p>
                    Well, the shortest answer would be this: HTML is the body,
                    CSS is the clothing, and JS is the brain. But let's
                    elaborate on that a bit more.
                  </p>
                  <p>
                    In the early days of the web, there was only HTML, the HTML
                    as its name suggests, was a text file capable of storing
                    more things than just plain text (but in the form of text!),
                    such as images or links to other websites. I, myself,
                    remember that era, the web was really ugly!
                  </p>
                  <p>
                    But then came the CSS, to make the HTML pretty. and boy it
                    did! The combination of HTML + CSS was perfect! (it still
                    is). Websites were pretty with lots of different and cool
                    designs and colors.
                  </p>
                  <p>
                    But some smart people somewhere in the world thought to
                    themselves, the web can yet be more! Back then websites were
                    only beautiful posters with links, they didn't do much! They
                    were basically electronic magazines uploaded on the
                    internet. But when the JS got added, they became a lot more!
                    It was the birth of web applications! The websites weren't
                    only pretty posters anymore, they were real, capable
                    software, quite similar to the ones locally installed on the
                    machine's OS but on the web!
                  </p>
                  <p>And the web is ever-evolving to this date.</p>
                </Answer>
              </QApair>
            </Accordion>
          </Item>

          <Item>
            <Accordion
              triggerId='question-02-button'
              bodyId='question-02-body'
              title='Question 02'
            >
              <QApair>
                <Question>
                  How would you briefly describe frontend to someone who has no
                  experience of it?
                </Question>

                <Answer>
                  <p>
                    As I have a passion for teaching (especially the
                    fundamentals) and have taught a few of my friends, I often
                    need to explain this exact subject. To put it simply, I go
                    with this, the front-end of any software (web, mobile,
                    desktop, ...) is the part visible to the end-user. What the
                    user sees on the screen is directly controlled by the
                    front-end.
                  </p>
                  <p>
                    If a piece of software can be compared to a car, the
                    front-end is the dashboard, the steering wheel, the pedals,
                    and all the buttons and controls. Other than just looking
                    good, it is responsible for the user experience. If you've
                    ever used a crappy volume knob on a car stereo that changes
                    the volume from 1 to 1000 on the slightest of rotations,
                    then you'd recognize and appreciate a well-damped knob that
                    gives you precision and control.
                  </p>
                  <p>
                    The same goes for software. There is much great software
                    with tons of features, but with terrible user interface and
                    user experience. They often end up rewritten or entirely
                    replaced with another software that does exactly the same
                    but is also easy to use and good-looking! Another thing
                    about the front-end from the development perspective is that
                    since it's visual, it's easier to start. Even kids can be
                    taught HTML and CSS and they would enjoy it. But it is sort
                    of a double-edged knife. Many get attracted to the front-end
                    because of the easiness to start, but then get overwhelmed
                    with the great number of tiny details they need to take care
                    of.
                  </p>
                  <p>
                    As a full-stack developer, most of the time I find working
                    in the front to be harder or better say more time-consuming
                    than in the back-end. When writing an app from scratch, I
                    always finish the API first. Although back-end code is much
                    riskier and you need to plan ahead many important things
                    (database backup, optimization, security, server
                    administration, load balancing, ...), but when you have the
                    correct response, you just have to send it away. You don't
                    care who's going to see it and on what device (a 90" TV or a
                    tiny old mobile phone). Your work is done and it is someone
                    else's trouble!
                  </p>
                  <p>
                    Because of that when I'm asked by someone (that has no
                    preference or special needs) who wants to start programming,
                    on what path should he/she take, I always suggest why don't
                    you try HTML and CSS and see if you like it. Then if they
                    wanted to go deeper, I warn them about the dangers ahead! So
                    if I want to describe the journey of learning front-end
                    development for someone who wants to start, I'd say it takes
                    a minute to learn (obviously not a minute!) but a lifetime
                    to master.
                  </p>
                </Answer>
              </QApair>
            </Accordion>
          </Item>

          <Item>
            <Accordion
              triggerId='question-03-button'
              bodyId='question-03-body'
              title='Question 03'
            >
              <QApair>
                <Question>Why is semantically correct HTML important?</Question>

                <Answer>
                  <p>
                    Well, one obvious reason for that is SEO. Anyone putting up
                    a website on the web, wants their website to be seen! and
                    the main way people will ever find them is via a search
                    engine. So, the website needs to be optimized for the search
                    engines, by using correct HTML tags.
                  </p>
                  <p>
                    But another benefit of using semantic tags, which I think is
                    much more important, from the human aspect, is
                    accessibility. The very soul of the web is to provide
                    information to everyone in the world. And by everyone, we
                    should consider literally everyone, not just the people like
                    us. Those less lucky than us who are going through life with
                    disabilities, making it harder for them than it already is.
                  </p>
                  <p>
                    I myself was quite ignorant to this fact. Until I was asked
                    by the good people in Soltia if I knew how to handle
                    accessibility. And I didn't! Cause I've never thought about
                    it before. None of the places I've worked cared about it
                    either! I've never used a screen reader before. But when I
                    did, and I heard that robotic voice reading the page,
                    imagining someone sitting in the dark and that robotic voice
                    reading the page, is their only window to the world, made me
                    realize the importance of it.
                  </p>
                  <p>
                    Now I understand that as a front-end developer, it is our
                    duty to guide that voice through our content and pave its
                    way, so in return, someone in the world sitting in the dark,
                    can have access to the information they need a bit easier.
                  </p>
                </Answer>
              </QApair>
            </Accordion>
          </Item>
        </List>
      </main>
    </>
  )
}

export default Task_02
