import styled from 'styled-components'
import Footer from './components/footer/Footer'
import Header from './components/header/Header'
import Router from './Router'
import { MaxWidth, PageContainer } from './styles/globalStyles'

const App = () => {
  return (
    <>
      <SkipToMainLink href='#main-content'>Skip to main content</SkipToMainLink>

      <Header />

      <PageContainer>
        <MaxWidth>
          <Router />
        </MaxWidth>
      </PageContainer>

      <Footer />
    </>
  )
}

export default App

/**
 * Hidden button to help navigate to the main content much faster for users who use keyboard navigation
 * instead of going through all the menu links or sidebar stuff they can go to main content directly
 */
const SkipToMainLink = styled.a`
  background-color: orangered;
  color: white;
  font-size: 1.25em;
  text-transform: uppercase;
  font-weight: bold;
  border-radius: 5px;
  width: 13em;
  height: 4em;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  left: -13em;
  z-index: 100;

  &:focus {
    left: 0;
  }
`
