import React, { useState } from 'react'
import { Body, Trigger, Wrapper } from './accordion_styles'

/**
 * Custom trigger with proper accessibility adjustments for screen readers
 */
const Accordion = ({ triggerId, bodyId, title, children }) => {
  const [Expanded, setExpanded] = useState(false)

  return (
    <>
      <Wrapper>
        <Trigger
          aria-expanded={Expanded}
          aria-controls={bodyId}
          id={triggerId}
          onClick={() => setExpanded(!Expanded)}
        >
          {title}
        </Trigger>
        <Body
          id={bodyId}
          role='region'
          aria-labelledby={triggerId}
          expanded={Expanded}
        >
          {children}
        </Body>
      </Wrapper>
    </>
  )
}

export default Accordion
