import { UsersList } from '../searchBar_styles'
import PropTypes from 'prop-types'
import dayjs from 'dayjs'
import UserListButton from './UserListButton'
import { RovingTabIndexProvider } from 'react-roving-tabindex'

/**************************************
 ******** Render users list
 *************************************/
const RenderUsersList = ({
  users,
  userInput,
  localSearchHistory,
  setLocalSearchHistory,
  setUserInput,
  setResultsIsVisible,
  setSelectedUser,
}) => {
  /**************************************
   ******** Select from list
   *************************************/
  const selectFromList = selectedId => {
    const { REACT_APP_LOCAL_HISTORY_KEY } = process.env

    // find selected user
    let selectedUser = users.find(({ id }) => id === selectedId)

    // add current data to selectedUser
    selectedUser['created_at'] = dayjs().format()

    // get local history from local storage
    let localHistory = JSON.parse(
      localStorage.getItem(REACT_APP_LOCAL_HISTORY_KEY)
    )

    // if local history is available add selected item to it
    if (localHistory) {
      // check for duplicates
      const duplicates = localHistory.find(({ id }) => id === selectedUser.id)

      if (!duplicates) {
        localHistory.push(selectedUser)
        localStorage.setItem(
          REACT_APP_LOCAL_HISTORY_KEY,
          JSON.stringify(localHistory)
        )

        // update local history state
        setLocalSearchHistory([...localSearchHistory, selectedUser])
      }
    }
    // else initialize it with an array with one element which is the selected user
    else {
      localHistory = [selectedUser]
      localStorage.setItem(
        REACT_APP_LOCAL_HISTORY_KEY,
        JSON.stringify(localHistory)
      )

      // update local history state
      setLocalSearchHistory([...localSearchHistory, selectedUser])
    }

    // update the state and close the panel and clear user input
    setUserInput('')
    setResultsIsVisible(false)
    setSelectedUser(selectedUser)
  }

  let filteredUsers = []

  users.forEach(user => {
    // check if user name includes any part of the user input
    if (user.name.toLowerCase().includes(userInput.toLowerCase()))
      filteredUsers.push(user)
  })

  return (
    <RovingTabIndexProvider options={{ direction: 'both' }}>
      <UsersList>
        {filteredUsers.map(({ id, name }) => (
          <UserListButton id={id} name={name} selectFromList={selectFromList} />
        ))}
      </UsersList>
    </RovingTabIndexProvider>
  )
}

RenderUsersList.propTypes = {
  users: PropTypes.array.isRequired,
  userInput: PropTypes.string.isRequired,
  localSearchHistory: PropTypes.array.isRequired,
  setLocalSearchHistory: PropTypes.func.isRequired,
  setUserInput: PropTypes.func.isRequired,
  setResultsIsVisible: PropTypes.func.isRequired,
  setSelectedUser: PropTypes.func.isRequired,
}

export default RenderUsersList
