import dayjs from 'dayjs'
import PropTypes from 'prop-types'
import {
  RemoveSearch,
  SearchHistory,
  SearchHistoryItem,
  SearchHistoryList,
} from '../searchBar_styles'

/**************************************
 ******** Render search history
 *************************************/
const RenderSearchHistory = ({
  localSearchHistory,
  setLocalSearchHistory,
  users,
  setSelectedUser,
}) => {
  /**************************************
   ******** Select user
   *************************************/
  const selectUser = idToChoose => {
    const selectedUser = users.find(({ id }) => id === idToChoose)
    setSelectedUser(selectedUser)
  }

  /**************************************
   ******** Remove from local history
   *************************************/
  const removeFromHistory = idToRemove => {
    // filter search history
    const filteredHistory = localSearchHistory.filter(
      ({ id }) => id !== idToRemove
    )

    // update state and local storage
    setLocalSearchHistory(filteredHistory)
    setSelectedUser({})
    localStorage.setItem(
      process.env.REACT_APP_LOCAL_HISTORY_KEY,
      JSON.stringify(filteredHistory)
    )
  }

  return (
    <SearchHistoryList>
      {localSearchHistory.map(({ id, name, created_at }) => (
        <SearchHistoryItem key={id}>
          <SearchHistory onClick={() => selectUser(id)}>
            <strong>{name}</strong>
            <span>{dayjs(created_at).format('YYYY-MM-DD HH:MM')}</span>
          </SearchHistory>

          <RemoveSearch
            aria-label='remove this user history'
            onClick={() => removeFromHistory(id)}
          >
            <span role='img' class='material-icons'>close</span>
          </RemoveSearch>
        </SearchHistoryItem>
      ))}
    </SearchHistoryList>
  )
}

RenderSearchHistory.propTypes = {
  users: PropTypes.array.isRequired,
  localSearchHistory: PropTypes.array.isRequired,
  setLocalSearchHistory: PropTypes.func.isRequired,
  setSelectedUser: PropTypes.func.isRequired,
}

export default RenderSearchHistory
