import styled from 'styled-components'

export const Wrapper = styled.div`
  margin: 0.5em 0;
`

export const Trigger = styled.button`
  width: 100%;
  height: 4em;
  background-color: #198754;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: none;
  border-radius: 5px;
  padding: 1.5em;
  cursor: pointer;
  transition: all ease-in 0.2s;

  &:hover {
    filter: brightness(1.1);
  }
`

export const Body = styled.div`
  line-height: 1.5em;
  display: ${({ expanded }) => (expanded ? 'default' : 'none')};
  padding: 0 1em;
  font-size: 1.25em;
  color: #5e5e5e;
`
