/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect, useState } from 'react'
import { JsonPlaceholderAPI } from '../../api/api_config'
import {
  Input,
  Loading,
  Section,
  ResultsWrapper,
  SearchHistoryRow,
  SearchWrapper,
} from './searchBar_styles'
import PropTypes from 'prop-types'
import RenderUsersList from './searchBar_components/RenderUsersList'
import RenderSearchHistory from './searchBar_components/RenderSearchHistory'
import RenderUserCard from './searchBar_components/RenderUserCard'

const SearchBar = ({ placeholder, apiCallDelay = 300 }) => {
  /**************************************
   ******** State
   *************************************/
  const [ResultsIsVisible, setResultsIsVisible] = useState(false)
  const [UserInput, setUserInput] = useState('')
  const [ApiCallTimer, setApiCallTimer] = useState(null)
  const [IsFetchingData, setIsFetchingData] = useState(false)
  const [Users, setUsers] = useState([])
  // initialize the state with the users stored in local storage or an empty array
  const [LocalSearchHistory, setLocalSearchHistory] = useState(
    JSON.parse(localStorage.getItem(process.env.REACT_APP_LOCAL_HISTORY_KEY)) ||
      []
  )
  const [SelectedUser, setSelectedUser] = useState({})

  /**************************************
   ******** Watch user input
   *************************************/
  useEffect(() => {
    // display results body if user input is not empty
    if (UserInput.length) {
      setResultsIsVisible(true)
      setIsFetchingData(true)

      // if the api call timer is set previously, clear it
      if (ApiCallTimer) clearTimeout(ApiCallTimer)

      // store the api call timer in the state
      setApiCallTimer(
        setTimeout(() => {
          getUsers()
        }, apiCallDelay)
      )
    }
    // hide results body if user input is empty
    else setResultsIsVisible(false)
  }, [UserInput])

  /**************************************
   ******** Get all users
   *************************************/
  const getUsers = async () => {
    const { data: users } = await JsonPlaceholderAPI.get('/users')
    setUsers(users)
    setIsFetchingData(false)
  }

  /**************************************
   ******** Clear search history
   *************************************/
  const clearSearchHistory = () => {
    setLocalSearchHistory([])
    setSelectedUser({})
    localStorage.removeItem(process.env.REACT_APP_LOCAL_HISTORY_KEY)
  }

  /**************************************
   ******** Render
   *************************************/
  return (
    <>
      <Section role='search'>
        <SearchWrapper>
          <Input
            role='searchbox'
            placeholder={placeholder}
            value={UserInput}
            onChange={({ target: { value: inputText } }) =>
              setUserInput(inputText)
            }
          />

          <ResultsWrapper visible={ResultsIsVisible}>
            {IsFetchingData ? (
              <Loading>Loading...</Loading>
            ) : (
              <RenderUsersList
                users={Users}
                userInput={UserInput}
                localSearchHistory={LocalSearchHistory}
                setLocalSearchHistory={setLocalSearchHistory}
                setUserInput={setUserInput}
                setResultsIsVisible={setResultsIsVisible}
                setSelectedUser={setSelectedUser}
              />
            )}
          </ResultsWrapper>

          <SearchHistoryRow>
            <strong>History</strong>
            <button onClick={clearSearchHistory}>Clear history</button>
          </SearchHistoryRow>

          <RenderSearchHistory
            users={Users}
            localSearchHistory={LocalSearchHistory}
            setLocalSearchHistory={setLocalSearchHistory}
            setSelectedUser={setSelectedUser}
          />
        </SearchWrapper>

        <RenderUserCard selectedUser={SelectedUser} />
      </Section>
    </>
  )
}

SearchBar.propTypes = {
  placeholder: PropTypes.string.isRequired,
  apiCallDelay: PropTypes.number,
}

export default SearchBar
