import React, { useRef } from 'react'
import { useRovingTabIndex, useFocusEffect } from 'react-roving-tabindex'
import { UserRow } from '../searchBar_styles'
import PropTypes from 'prop-types'

const UserListButton = ({ id, name, selectFromList, disabled = false }) => {
  // The ref of the input to be controlled.
  const ref = useRef(null)

  // handleKeyDown and handleClick are stable for the lifetime of the component:
  const [tabIndex, focused, handleKeyDown, handleClick] = useRovingTabIndex(
    ref, // Don't change the value of this ref.
    disabled // But change this as you like throughout the lifetime of the component.
  )

  // Use some mechanism to set focus on the button if it gets focus.
  // In this case I use the included useFocusEffect hook:
  useFocusEffect(focused, ref)

  return (
    <UserRow
      key={id}
      ref={ref}
      tabIndex={tabIndex}
      disabled={disabled}
      onKeyDown={e => {
        handleKeyDown(e)
        if (e.key === 'Enter') selectFromList(id)
      }}
      onClick={e => {
        handleClick(e)
        selectFromList(id)
      }}
    >
      {name}
    </UserRow>
  )
}

UserListButton.propTypes = {
  id: PropTypes.number.isRequired,
  name: PropTypes.string.isRequired,
  selectFromList: PropTypes.func.isRequired,
  disabled: PropTypes.bool,
}

export default UserListButton
