import React from 'react'
import { Link } from 'react-router-dom'
import { HeaderLink, Logo, Wrapper } from './header_styles'

const Header = () => {
  return (
    <>
      <Wrapper>
        <Link to='/'>
          <Logo src='https://admin.soltia.com/uploads/Soltia_logo_6641f18ff6_a59658a49b.png' alt='Soltia Logo' />
        </Link>

        <Link to='/'>
          <HeaderLink>Home</HeaderLink>
        </Link>
      </Wrapper>
    </>
  )
}

export default Header
