import styled from 'styled-components'

export const Title = styled.h3`
  font-size: 1.75em;
  font-weight: bold;
  color: #3d60c0;
  margin-top: 7em;
`

export const Main = styled.main`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 3em;
  width: 100%;
`

export const MainTitle = styled.h1`
  letter-spacing: 0.5em;
  color: #292929;
  font-size: 2.5em;
  text-align: center;
  text-transform: uppercase;
  font-weight: 900;
  margin: 3em auto;
  
  @media (max-width: 800px) {
    letter-spacing: 0.2em;
    font-size: 1.85em;
  }
`

export const ButtonsRow = styled.nav`
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1em;
  max-width: 50em;
  margin: auto;
  
  @media (max-width: 800px) {
    grid-template-rows: repeat(3, 1fr);
    grid-template-columns: 1fr;
    width: 100%;
  }
`

/**
 * I decided to use div elements here instead of buttons 
 * because these elements will be wrapped with links on the homepage
 * and then keyboard navigation would need two tabs to jump over each one if buttons were used
 */
export const Button = styled.div`
  background-color: #a2d1b3;
  border: 2px solid #aee6c0;
  padding: 0.75em 2.5em;
  border-radius: 50px;
  cursor: pointer;
  transition: all ease-in 0.1s;

  &:hover {
    transform: scale(1.05);
  }

  span {
    text-shadow: #00000082;
    font-size: 1.5em;
    color: white;
  }

  @media (max-width: 800px) {
    width: 100%;
  }
`
